import React from "react";
// importing hook
import { useCharacter } from "../../hooks/useCharacter";
// use params to be able to pass the ID as a parameter
import { useParams } from "react-router";

const Character = () => {
  // destructure loading, data and error from useCharacter
  const { id } = useParams();
  const { loading, data, error } = useCharacter(id);
  if (loading) return <div>spinner...</div>;
  if (error) return <div>Oops! my bad!</div>;
  // use Params to get the id and use it as a parameter

  const character = data.character;
  console.log(character);
  return (
    <>
      {/* pass in character data within the specific fields */}
      <div className="flex  w-full h-100 justify-center items-center">
        {/* text */}
        <div className="text-3xl">
          <div className="flex">
            <p className="mx-3">Name:</p>
            <p className="mx-3">{character.name}</p>
          </div>
          <div className="flex">
            <p className="mx-3">Gender:</p>
            <p className="mx-3">{character.gender}</p>
          </div>
          <div className="flex">
            <p className="mx-3">Origin:</p>
            <p className="mx-3">{character.origin.name}</p>
          </div>
        </div>
        {/* image */}
        <div>
          <img className="rounded-3xl w-full" src={character.image} />
        </div>
      </div>
    </>
  );
};

export default Character;
