import React from "react";
import "./CharacterList.css";
import useCharacters from "../../hooks/useCharacters";
import { Link } from "react-router-dom";
import { motion } from "framer-motion";

// when a user clicks on a specific page - re-route them to the other page

const CharactersList = () => {
  // calling the useCharacters Hook
  const { data, loading, error } = useCharacters();

  // if loading return spinner
  if (loading) return <div>spinner...</div>;
  if (error) return <div>Oops! my bad!</div>;

  return (
    <>
      <div className="flex flex-col">
        <div className="flex justify-center">
          {/* <Link
            class="text-white bg-gradient-to-br from-purple-600 to-blue-500 hover:bg-gradient-to-bl focus:ring-4 focus:outline-none focus:ring-blue-300 dark:focus:ring-blue-800 font-medium rounded-lg 
        text-2xl px-5 py-2.5 text-center mr-2 mb-2"
            to="/search"
          >
            Search Locations!
          </Link> */}
        </div>
        <div className="flex flex-wrap justify-center">
          {data.characters.results.map((character) => {
            return (
              <Link to={`/${character.id}`} className="m-8">
                <motion.img
                  whileHover={{
                    scale: 1.05,
                    transition: { duration: 1 },
                  }}
                  className="rounded-3xl hover:shadow-xl"
                  src={character.image}
                />
                <h2
                  className="font-bold text-xl text-[#11afc8] text-center
            my-3"
                >
                  {character.name}
                </h2>
              </Link>
            );
          })}
        </div>
      </div>
    </>
  );
};

export default CharactersList;
